<template>
  <div>
    <NavHeader/>
    <div class="section">
       <div>
         <transition name="btn">
            <router-view/>
         </transition>
       </div>
    </div>
    <NavFooter/>
  </div>
</template>

<script>
// @ is an alias to /src
import NavHeader from '@/components/NavHeader.vue'
import NavFooter from '@/components/NavFooter.vue'

export default {
  data:function(){
     return {
         mdShow:false,
     }
  },
  methods:{
    closeModal(){
         this.mdShow = false;
    },
    showFilterPop(){//true
         this.filterBy=true;
         this.overLayFlag=true;
    },
    closePop(){//false
         this.filterBy=false;
         this.overLayFlag=false;
         this.mdShowCart = false;
    }
  },
  components: {
    NavHeader,
    NavFooter
  },
  watch:{

  },
  mounted() {
    //jsdom
    //bus
  },
}
</script>
<style scoped>
.section{
    width:100%;
    background:rgb(67,67,67);
}
.btn-enter-active{
  /*  */
    transition: linear all .5s;
}
/*  */
.btn-enter{
    opacity:0;
}
/*  */
.btn-enter-to{
    opacity:1;
}

</style>



